.sections-wrapper {
  position: relative;
  overflow-x: hidden; }

.iScrollIndicator {
  background-color: $blue !important; }

.section {
  // height: calc(100vh - 135px) !important
  // padding-top: 135px
  &__outer {}

  &__inner {
    height: calc(100vh - 135px);
    // min-height: 100vh
    display: flex;
    flex-wrap: wrap;
 }    // margin: 0 -15px
  &__left {
    @include flex-size(27.5%);
    padding: 0 88px 0 0;
    &_border {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        right: 0;
        top: 45px;
        bottom: 37px;
        width: 1px;
        background-color: $textLight; } } }
  &__right {
    @include flex-size(72.5%);
    padding-left: 156px;
    position: relative; }
  &__splitter-horiz {
    height: 1px;
    width: 100%;
    background-color: $text;
    &_white {
      background-color: $textLight; } }
  &__title-lg {
    margin-bottom: 60px; }
  &__title-md {
    margin-top: 210px;
    margin-bottom: 50px; }
  .section__text {
    margin-bottom: 60px;
    p:not(:last-child) {
      margin-bottom: 24px; } }
  .fp-tableCell {
    vertical-align: top; }
  &_selling {
    .section__left {
      @include cover; }
    .section__text {
      margin-top: 50px;
      p:not(:last-child) {
        margin-bottom: 24px; } } }
  &__download-btn {
    display: inline-block;
    position: relative;
    height: 50px;
    &:after {
      content: '';
      position: absolute;
      top: -50px;
      left: -50px;
      width: 150px;
      bottom: -50px;
      border-radius: 50%;
      background-color: rgba(red, 0.1);
      z-index: 1; }
    .circle-btn {
      position: relative;
      z-index: 3; } }
  &__hr-btn {
    margin-top: 103px; }
  &_investment,
  &_career {
    .section__inner {
      background-color: $darkBg;
      background-position: 0 100%;
      background-repeat: no-repeat; } }
  &_development {
    .section__inner {
      background-color: $textLight; } }
  &_contacts {
    color: $white;
    .section__inner {
      background-color: $blue; }
    .section__right {
      // background-color: $darkBlue
      padding-left: 0;
      background-position: center;
      background-size: contain;
      // background-position: 0 0
      // background-size: cover
      @include cover; } }
  &__adress {
    margin-bottom: 60px;
    .adress:not(:last-child) {
      margin-bottom: 24px; } }
  &__contacts-title {
    margin-top: 100px;
    margin-bottom: 50px; }
  &__connections {
    margin-top: 50px;
    .connection {
      &:not(:last-child) {
        margin-bottom: 20px; } } }
  &__map {
    max-width: 100%; } }

.section_development {
  position: absolute;
  top: 50vh;
  left: 14.1%;
  z-index: 3;
  width: 100%;
  min-height: inherit;
  height: calc(100vh - 135px);
  transition: transform 0.5s;
  @include hover {
    transform: translateY(-50vh); } }

.section_investment {
  position: relative;
  z-index: 1;
  min-height: inherit;
  height: calc(100vh - 135px); }

.section_career {
  min-height: inherit;
  height: calc(100vh - 135px);
  position: relative;
  z-index: 5;
  margin-top: 92px; }

