body {
  line-height: 1.4;
  font-family: $gilroy;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.56;
  letter-spacing: -0.005em;
  color: $text;
  padding-top: 135px !important;
  opacity: 0;
  transition: opacity 0.2s;
  &.is-loaded {
    opacity: 1; } }
a {
  color: inherit;
  text-decoration: none; }

.text-block {
  max-width: 590px; }
